const menuCards = () => {
    class Card {
        constructor(src, alt, tittle, descr, price, parentSelector, ...classes) {
            this.src = src;
            this.alt = alt;
            this.tittle = tittle;
            this.descr = descr;
            this.price = price;
            this.classes = classes;
            this.parentElement = document.querySelector(parentSelector);
            this.transfer = 60;
            this.changeToRub();
        }
        changeToRub() {
            return this.price *= this.transfer;
        }

        render() {
            const cardItem = document.createElement('div');
            if (this.classes.length === 0) {
                this.classes = 'menu__item';
                cardItem.classList.add(this.classes);
            } else {
                this.classes.forEach(eachClass => cardItem.classList.add(eachClass));
            }
            cardItem.innerHTML = `
                <img src=${this.src} alt=${this.alt}>
                <h3 class="menu__item-subtitle">${this.tittle}</h3>
                <div class="menu__item-descr">${this.descr}</div>
                <div class="menu__item-divider"></div>
                <div class="menu__item-price">
                    <div class="menu__item-cost">:</div>
                    <div class="menu__item-total"><span>${this.price}</span> /</div>
                </div>
            `;
            this.parentElement.append(cardItem);
        }
    }

    new Card(
        "img/tabs/vegy.jpg",
        "vegy",
        ' ""',
        ' "" -      :     .     .          !',
        9,
        '.menu__field .container'
    ).render();

    new Card(
        "img/tabs/elite.jpg",
        "elite",
        ' ""',
        '  ""       ,     .  , ,  -      !',
        20,
        '.menu__field .container',
        'menu__item'
    ).render();

    new Card(
        "img/tabs/post.jpg",
        "post",
        ' ""',
        '  -    :     ,   , ,   ,          .',
        15,
        '.menu__field .container',
        'menu__item'
        ).render();
};

export default menuCards;